	module.exports = {"data":{"markdownRemark":{"html":"<p>An interactive data visualization of Canada's jobs.</p>\n<p>Using Tableau, this prototype is the third-place winner of the <a href=\"https://studentpathwayschallenge.ca/\">Student Pathways Challenge</a>, a design competition to help high school students make an informed decision about their professional pursuits.</p>\n<p>My team performed user research, competitive analysis of existing solutions, and two rounds of user testing. Our research revealed that in contrast to the prescriptive '20-minute career quiz' approach, students would benefit from a low-commitment, user-first, and engaging exploration of the available career landscape.</p>\n<p>Using parameters such as income, duration of study, job security, and skills used on the job, Pave takes overwhelming data and allows users to quickly drill down into the areas that interest them.</p>","frontmatter":{"id":"1","path":"/2018-1__Pave-Tableau","caption":"Pave is an interactive exploration of Canada's jobs. This prototype placed 3rd in the Student Pathways Challenge.","title":"Pave Prototype 1: Interactive Data Visualization","year":"2018","radius":"5","tools":["Excel","Tableau"],"image":"https://image.ibb.co/jSKV1e/pave_tableau.jpg","imgThumb":"https://thumb.ibb.co/fRdXge/pave_tableau_logo.jpg","website":"https://public.tableau.com/profile/daniel.corner#!/vizhome/PavePrototype4_1/Welcome"}}},"pathContext":{}}

